@import "sass/index";

.wrapper {
  padding-bottom: spacing(2);
}
.row {
  display: flex;
  flex-direction: row;
  margin-top: spacing(2);

  @media (max-width: 460px) {
    flex-direction: column;
  }
}
.nonceWrapper {
  margin-left: spacing(2);
  max-width: 160px;

  @media (max-width: 460px) {
    margin: spacing(2) 0;
    max-width: 100%;
  }
}
.buttonWrapper {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  align-content: center;
  justify-content: center;
  width: 100%;
  gap: 10px;
  grid-gap: 10px;
}
.textLink {
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  text-decoration: none;
  font-weight: bold;
  font-size: 1rem;
  justify-self: center;
  background: rgba($m_table-body-mobile, 0.5) !important;
  color: white;

  & > * {
    transition: transform 1s;
  }

  & > *:hover {
    transform: scale(1.075);
  }
}
.text {
  text-overflow: ellipsis;
  overflow: hidden;
  width: max-content;
  margin: 5px 0;
}

.dialogActions {
  padding-top: spacing(4.5);
  padding-bottom: spacing(2);
  padding-right: spacing(5.5);
  padding-left: spacing(5.5);
}
.title {
  font-family: "Gilroy";
  font-weight: bold;
  font-size: 24px;
  color: white;
  letter-spacing: 2px;
  text-transform: uppercase;
  margin: 0;
}

.grow {
  flex: 1 1 auto;
}

.contentRoot {
  padding: 0 spacing(4.5);
}
.contentInnerWrapper {
  width: 100%;
  max-width: 100%;
  padding: 0 0 spacing(2.5);
}
.content {
  padding: 0;
  overflow: visible;
}

.input {
  color: black;
}
